import { createHigherOrderComponent } from '@wordpress/compose';
import { InspectorControls } from '@wordpress/block-editor';
import { addFilter } from '@wordpress/hooks';
import {
	ToggleControl,
	Panel,
	PanelBody,
	PanelRow,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

// if not excluded then it causes render error as adding custom attributes to these blocks are prohibited.
const excludeBlock = [
	'core/archives',
	'core/calendar',
	'core/latest-comments',
	'core/tag-cloud',
	'core/rss',
];

const Responsive = createHigherOrderComponent( ( BlockEdit ) => {
	return ( props ) => {
		const { attributes, name, setAttributes } = props;
		// Adding compatibility with frontis plugin. So the slugs are same as the plugin.
		const { FBHideDesktop, FBHideTab, FBHideMob } = attributes;

		if ( name && name.includes( 'core/' ) && ! excludeBlock.includes( name ) ) {
			return (
				<>
					<BlockEdit { ...props } />
					<InspectorControls>
						<Panel>
							<PanelBody
								title="Responsive Settings"
								initialOpen={ true }
							>
								<PanelRow>
									<ToggleControl
										label={ __(
											'Hide Desktop',
											'frontis-block'
										) }
										checked={ FBHideDesktop }
										onChange={ () =>
											setAttributes( {
												FBHideDesktop: ! FBHideDesktop,
											} )
										}
									/>
								</PanelRow>

								<PanelRow>
									<ToggleControl
										label={ __( 'Hide Tablet', 'frontis-block' ) }
										checked={ FBHideTab }
										onChange={ () =>
											setAttributes( {
												FBHideTab: ! FBHideTab,
											} )
										}
									/>
								</PanelRow>
								<PanelRow>
									<ToggleControl
										label={ __( 'Hide Mobile', 'frontis-block' ) }
										checked={ FBHideMob }
										onChange={ () =>
											setAttributes( {
												FBHideMob: ! FBHideMob,
											} )
										}
									/>
								</PanelRow>
							</PanelBody>
						</Panel>
					</InspectorControls>
				</>
			);
		}
		return (
			<>
				<BlockEdit { ...props } />
			</>
		);
	};
}, 'Responsive' );

addFilter( 'editor.BlockEdit', 'fb/responsive', Responsive );

function ResponsiveAttributes( settings ) {
	const { name, attributes } = settings;

	if ( name && name.includes( 'core/' ) && ! excludeBlock.includes( name ) ) {
		if ( attributes ) {
			settings.attributes = Object.assign( attributes, {
				FBHideDesktop: {
					type: 'boolean',
					default: false,
				},
				FBHideTab: {
					type: 'boolean',
					default: false,
				},
				FBHideMob: {
					type: 'boolean',
					default: false,
				},
			} );
		}
	}

	return settings;
}

addFilter(
	'blocks.registerBlockType',
	'fb/responsive-attributes',
	ResponsiveAttributes
);

const responsiveCss = createHigherOrderComponent( ( BlockListBlock ) => {
	return ( props ) => {
		const { name, attributes } = props;

		if ( name && name.includes( 'core/' ) && ! excludeBlock.includes( name ) ) {
			const { FBHideDesktop, FBHideTab, FBHideMob } = attributes;
			return (
				<>
				<style>
					{`@media (min-width: 1025px) {
						.fb-hide-desktop {
							background: repeating-linear-gradient(125deg, rgba(0, 0, 0, .05), rgba(0, 0, 0, .05) 1px, transparent 2px, transparent 9px);
							opacity: .4;
						}
					}

					@media (min-width: 768px) and (max-width: 1024px) {
						.fb-hide-tablet {
							background: repeating-linear-gradient(125deg, rgba(0, 0, 0, .05), rgba(0, 0, 0, .05) 1px, transparent 2px, transparent 9px);
							opacity: .4;
						}
					}

					@media (max-width: 767px) {
						.fb-hide-mobile {
							background: repeating-linear-gradient(125deg, rgba(0, 0, 0, .05), rgba(0, 0, 0, .05) 1px, transparent 2px, transparent 9px);
							opacity: .4;
						}
					}`}
				</style>
					<BlockListBlock { ...props } className={ `${ props?.className ? `${ props.className } ` : '' }${ FBHideDesktop ? 'fb-hide-desktop ' : '' }${ FBHideTab ? 'fb-hide-tablet ' : '' }${ FBHideMob ? 'fb-hide-mobile ' : '' }` } />
				</>

			);
		}

		return (
			<>
				<BlockListBlock { ...props } />
			</>
		);
	};
}, 'responsiveCss' );

addFilter(
	'editor.BlockListBlock',
	'fb/responsive-css',
	responsiveCss
);

