import { createHigherOrderComponent } from '@wordpress/compose';
import { InspectorControls } from '@wordpress/block-editor';
import { CheckboxControl } from '@wordpress/components';
import { addFilter } from '@wordpress/hooks';
import {
    Panel,
    PanelBody,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';

// if not excluded then it causes render error as adding custom attributes to these blocks are prohibited.
const excludeBlock = [
    'core/archives',
    'core/calendar',
    'core/latest-comments',
    'core/tag-cloud',
    'core/rss',
];

const fbImgFluid = createHigherOrderComponent( ( BlockEdit ) => {
    return ( props ) => {

        const { attributes, name, setAttributes } = props;
        // Adding compatibility with frontis plugin. So the slugs are same as the plugin.
        const { imgFull } = attributes;

        const setImgFluid = () => {
            setAttributes({imgFull: !imgFull})
        }

        if ( name && name.includes( 'core/image' )) {
            return (
                <>
                    <BlockEdit { ...props } />
                    <InspectorControls>
                        <Panel>
                            <PanelBody
                                title="Image size"
                                initialOpen={ true }
                            >
                                <CheckboxControl
                                    __nextHasNoMarginBottom
                                    label="Full"
                                    checked={ imgFull }
                                    onChange={ setImgFluid }
                                />
                            </PanelBody>
                        </Panel>
                    </InspectorControls>
                </>
            );
        }
        return (
            <>
                <BlockEdit { ...props } />
            </>
        );
    };
}, 'fbImgFluid' );

addFilter( 'editor.BlockEdit', 'fb/fbimgfluid', fbImgFluid );

function FbImgFluidAttr( settings ) {
    const { name, attributes } = settings;

    if ( name && name.includes( 'core/image' )) {
        if ( attributes ) {
            settings.attributes = Object.assign( attributes, {
                imgFull: {
                    type: 'boolean',
                    default: false,
                }
            } );
        }
    }

    return settings;
}

addFilter(
    'blocks.registerBlockType',
    'fb/imgfluid-attributes',
    FbImgFluidAttr
);

const imgFluidCss = createHigherOrderComponent( ( BlockListBlock ) => {
    return ( props ) => {
        const { name, attributes } = props;

        if ( name && name.includes( 'core/image' ) ) {
            const { imgFull } = attributes;

            console.log(imgFull)
            return (
                <>
                <style>
                    {`
                        body .wp-block-image.fbimgfull img {
                            width: 100%;
                        }
                    `}
                </style>
                    <BlockListBlock { ...props } className={`${imgFull ? 'fbimgfull': ''}`} />
                </>

            );
        }

        return (
            <>
                <BlockListBlock { ...props } />
            </>
        );
    };
}, 'imgFluidCss' );

addFilter(
    'editor.BlockListBlock',
    'fb/imgFluidCss',
    imgFluidCss
);

